import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/sassNote',
    name: 'SassNote',
    component: () => import(/* webpackChunkName: "about" */ '../views/SassNote.vue')
  },
  {
    path: '/webpackNote',
    name: 'WebpackNote',
    component: () => import(/* webpackChunkName: "about" */ '../views/WebpackNote.vue')
  },
  {
    path: '/gitNote',
    name: 'GitNote',
    component: () => import(/* webpackChunkName: "about" */ '../views/GitNote.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
